.five-men {
    background-color: #F5F5F5;
    
    &_content {
        margin: auto;
        max-width: 460px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        background-color: #F5F5F5;

        img {
            height: 45px;
            width: 45px;
            cursor: pointer;
        }
    }
}